<template>
  <div class="home">
   <p>Welcome to the home page</p>
   <div v-for="vault in vaults" v-bind:key="vault._id">
     <a @click="showKeeps(vault)"> 
     {{vault.description}}
     </a>
   </div>
  </div>
</template>

<script>
import router from "../router"
export default {
  name: 'Home',
  data () {
    return {
    //   msg: 'Welcome to Your Vue.js App'
     user: {},
     
    
    }
  },
  mounted() {
  //  debugger
      if( this.$store.state.user.data == undefined || !this.$store.state.user.data.id)  //doesnt work if you are not logged in and trying to access a page. 
      {
          router.push({name: "Login"})
      }

    this.$store.dispatch("getUserVaults")


  }, 
  computed: {
    vaults(){
    
      return this.$store.state.userVaults
    }
  },
  methods: {
    showKeeps(vault){
      

    }
  } 
 

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



